<template>
  <div class="lightbox-alpha" v-on:click="closeModal()">
    <div class="lightbox-content" v-on:click.stop="" v-html="content"></div>
  </div>
</template>

<script>
export default {
  name: 'modal',
  props: ['closeModal', 'content'],
}
</script>

<style>
.lightbox-alpha {
  background: rgba(245, 245, 245, .85);
}

.lightbox-content {
  background-color: whitesmoke;
  padding: 10px;
}
</style>


